html, body {
  color: #060607;
  container-type: inline-size;
  font: normal 125%/1.6 Nunito, system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
  height: 100%;
  margin: 0;
  padding: 0;
}

html.admin-bar {
  height: calc(100% - 32px);
}
@media only screen and (max-width: 782px) {
  html.admin-bar {
    height: calc(100% - 46px);
  }
}

a:link, a:visited {
  color: #5d2189;
}
a:active, a:focus, a:hover {
  color: #c77093;
}

body {
  display: grid;
  block-size: 100%;
  font-size: 0.9rem;
  grid-template-rows: auto 1fr auto;
  min-block-size: 100%;
}

main {
  --max-width: 1000px;
  --min-margin: 1em;
  margin-inline: max(var(--min-margin), 50cqw - var(--max-width) / 2);
}
main .full-bleed {
  margin-inline: min(0px, var(--max-width) / 2 - 50cqw + var(--min-margin));
}

ul.menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.hidden {
  display: none;
  visibility: hidden;
}